import { Component, OnInit } from '@angular/core';
import { HomefetcherService } from '../../services/homefetcher/homefetcher.service';
import { PagerService } from '../../services/pager.service';
import { NgProgress } from '@ngx-progressbar/core';

@Component({
  selector: 'app-homes',
  templateUrl: './homes.component.html',
  styleUrls: ['./homes.component.css']
})
export class HomesComponent implements OnInit {

  homes: any;
  error: String;
  pagedItems: any;
  totalHomes: number;
  pager: any;
  constructor(private homefetcher: HomefetcherService, private paginator: PagerService, private pBar: NgProgress) { }

  ngOnInit() {
    this.pBar.start();
    this.pBar.set(5);
    this.homefetcher.fetchHomes().subscribe(homes => {
      this.homes = homes;
      this.totalHomes = this.homes.length;
      this.setPage(1);
      console.log(homes);
      this.pBar.complete();
    }, err => {
      this.error = err;
      console.log(err);
      this.pBar.complete();

    });
  }
  setPage(pNo: number) {
    console.log(this.totalHomes);
    this.pager = this.paginator.getPager(this.totalHomes, pNo);
    console.log(this.pager);
    this.pagedItems = this.homes.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }

}
