import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { nodeRoutes } from '../node_routes/node_routes';
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class HomefetcherService {

  constructor(private http: Http) { }
  fetchHomes() {
    console.log(nodeRoutes.fetchHomes);
    return this.http.post(nodeRoutes.fetchHomes, {}).pipe(map(res => res.json()));
  }
}
