import { Component, OnInit } from '@angular/core';
import { Headers, RequestOptions } from '@angular/http';
import { FileuploaderService } from '../../services/fileuploader/fileuploader.service';
import { NgProgress } from '@ngx-progressbar/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { timeout } from 'rxjs/operators';

@Component({
  selector: 'app-fileupload',
  templateUrl: './fileupload.component.html',
  styleUrls: ['./fileupload.component.css']
})
export class FileuploadComponent implements OnInit {

  constructor(private uploader: FileuploaderService, private pBar: NgProgress, private msg: FlashMessagesService) { }

  ngOnInit() {

  }
  uploadFileToServer($event) {
    let fileList: FileList = $event.target.files;
    if (fileList.length) {
      let file = fileList[0];
      let formData: FormData = new FormData();
      formData.append('uploadFile', file, file.name);
      let headers = new Headers();
      headers.append('Content-Type', 'multipart/form-data');
      headers.append('Accept', 'application/json');
      this.pBar.start();
      this.uploader.uploadToServer(formData, headers).subscribe(data => {
        this.msg.show("File Uploaded!", { cssClass: 'alert-info', timeout: 2000 });
        this.pBar.complete();
      }, err => {
        this.msg.show("error occured", { cssClass: 'alert-danger', timeout: 2000 });

        this.pBar.complete();
      });
    }

  }

}
