import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';
import { nodeRoutes } from '../node_routes/node_routes';
@Injectable({
  providedIn: 'root'
})
export class FileuploaderService {

  constructor(private http: Http) { }

  uploadToServer(formdata, headers) {
    return this.http.post(nodeRoutes.fileUpload, formdata, headers).pipe(map(res => res.json()));
  }
}
